import { Colors } from '../../../data/Colors';
import { BaseCommand, CommandType } from './_BaseCommand';
export class StarvingCommand extends BaseCommand {
    constructor(crawler, config, knowledge) {
        super(crawler, config, knowledge);
        this.config = config;
        this.finishPath = () => {
            if (this.currentPath) {
                this.isComplete = true;
            }
            else {
                this.state = 'idle';
                this.startIdleLoop();
            }
        };
        this.cancelPath = () => {
            this.state = 'idle';
            this.startIdleLoop();
        };
        this.idleComplete = () => {
            this.isComplete = true;
        };
        this.type = CommandType.STARVING;
        this.color = Colors.Node.darkblue;
    }
    initialize() {
        this.isComplete = false;
        this.state = 'walk';
        this.startPath(this.makesFood, this.finishPath, this.cancelPath);
    }
    genPriority() {
        return 20;
    }
    update() {
        if (this.isComplete)
            return;
        if (this.state === 'idle') {
            this.updateIdle(this.idleComplete);
        }
        else {
            this.updatePath();
        }
    }
    makesFood(node) {
        return (node.power.fruitType === 'food');
    }
}
