import { Config } from '../../Config';
export class PlantNodePhysics {
    constructor(config, view) {
        this.config = config;
        this.view = view;
        this._iMass = 0;
        this.vR = 0.005;
        this.fixed = false;
        this.hasMass = true;
        this.moveBody = () => {
            this.rotate();
            if (this.fixed)
                return;
            this.vX *= Config.PHYSICS.DAMP;
            this.vY *= Config.PHYSICS.DAMP;
            if (Math.abs(this.vX) > Config.PHYSICS.MIN_V || Math.abs(this.vY) > Config.PHYSICS.MIN_V) {
                this.view.x += this.vX;
                this.view.y += this.vY;
            }
            else {
                this.vX = 0;
                this.vY = 0;
            }
        };
        this._iMass = 1 / config.mass;
    }
    get force() {
        return this.config.force;
    }
    get iMass() {
        return this.hasMass ? this._iMass : 5;
    }
    rotate() {
        this.view.sprite.rotation += this.vR;
        if (this.view.sprite.rotation > Math.PI)
            this.view.sprite.rotation -= Math.PI * 2;
        if (this.view.sprite.rotation < -Math.PI)
            this.view.sprite.rotation += Math.PI * 2;
    }
}
